  display: block;
  font-size: 0.75rem;
  color: #9ab2cb;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  text-transform: uppercase;
  color: #d0dbe7;
  line-height: 2rem;
  color: #577fa8;
}
